export default function equipment(state=[
    ["Księga badań Ismeny", 2, 0, "Oto dzieło Twojego życia. Księga z którą nigdy się nie rozstajesz. Zapiski, które się w niej znajdują, są zbyt cenne by znalazł się jakikolwiek skarbiec na tym świecie zdolny ją ukryć przed chciwymi spojrzeniami. Dla większego bezpieczeństwa nałożyłaś na księgę mnóstwo zaklęć i pułapek, żeby potencjalny złodziej miał paskudną niespodziankę.", "book", 1],
    ["Torba", 0.5, 5, "Zwykła torba podróżnicza do przechowywania przeróżnych przedmiotów.", "item", 1],
    ["Zapasy jedzenia", 1, 1, "Zwykłe zapasy jedzenia, chleb, wędlina, ser, parę owoców. Starcza na jeden dzień pełen przygód.", "item", 2],
    ["Mikstura leczenia", 0.5, 50, "Każdy, kto wypije magiczny czerwony płyn natychmiast odzyskuje 2k4 + 2 PW.", "potion", 1],
    ["Sztylet", 1, 2, "Stalowy sztylet z bardzo dobrze wyważoną rękojeścią. Idealny dla złodziei i magów, którzy niewiele angażują się bezpośrednio w walkę.", "dagger", 1, "1k4 kłute"],
    ["Ubiór podróżnika", 2, 5, "Zwykły ubiór przygotowany specjalnie dla ludzi, dla których podróżowanie stało się codziennością.", "clothes", 1]
], action) {
    switch(action.type) {
        case 'ADD_ITEM':
            const find = state.findIndex(i => i.toString() === action.data.toString());
            if (find >= 0) {
                const x = [...state];
                x.map(m => {
                    if (m.toString() === action.data.toString()) {
                        m[5] += action.data[5];
                        return m;
                    }
                    return m;
                })
                return state = x;
            } else {
                return state.concat([action.data]);
            }
        case 'REMOVE_ITEM':
            if (action.data[5] === 1) {
                return state.filter(f => f.toString() !== action.data.toString());
            } else {
                const x = [...state];
                x.map(m => {
                    if (m === action.data) {
                        m[5] -= 1;
                        return m;
                    }
                    return m;
                })
                return state = x;
            }
        default:
            return state;
    }
}