export default function effects(state=[
    ["Zmęczenie", "Postać otrzymuje utrudnienie w rzutach opartych na Sile, Zręczności i Kondycji"]
], action) {
    switch(action.type) {
        case 'ADD_EFFECT':
            return state.concat([action.data]);
        case 'REMOVE_EFFECT':
            return state.filter(f => f.toString() !== action.data.toString());
        default:
            return state;
    }
}