export default function quests(state=[["Wyższe Tajemnice", "Odwiedź Wieżę Tajemnych Arkan w mieście Luskan.", 20]], action) {
    switch(action.type) {
        case 'ADD_QUEST':
            return state.concat([action.data]);
        case 'REMOVE_QUEST':
            return state.filter(f => f.toString() !== action.data.toString());
        default:
            return state;
    }
}